import React, { Suspense, lazy } from 'react';
import styled from "styled-components";
import Spinner from "../spinner/index";
const TotalCount = lazy(() => import("../totalBlock/total"));
const MainChartBlock = lazy(() => import("../mainChartBlock/mainChartBlock"));


const RootEl = styled.div`
	padding:0;
    width:80vw;
    max-width:80vw;
    border-bottom:0.2em solid black;
    min-height:20vw;
    box-sizing:border-box;
    display: flex;
    @media screen and (max-width: 600px){
    	display: block;
    }
    overflow:hidden;
`;


function TopRow() {
	return (
		<RootEl>
			<Suspense fallback={<Spinner/>}>
				<MainChartBlock/>
				<TotalCount/>
			</Suspense>
		</RootEl>
	);
}

export default TopRow;
