import styled from "styled-components";

export default  styled.div`
	box-sizing:border-box;
	background:${props => props.isError ? '#dc1b86':'#a3dcbe'};
	min-width:40%;
	border:1px;
	&:first-of-type{
		border-right:0.16em solid black;
		flex-grow:3;
		@media screen and (max-width: 600px){
			border-right:0;
	    	border-bottom:0.16em solid black;
    	}
	}
	
	margin:0;
	color:${props => props.isError ? '#faf3e3':'#247066'};
	overflow:hidden;
	padding:1vw;
	display:${props=>props.block ? "block":"flex"};
	${props=>props.borderLeft ? "border-left:0.16em solid black;": ""}
	
	
`;