import React, {Component} from 'react';
import styled from "styled-components";
import NewUsersChartBlock from "./NewUsers.chart";
import UsingChartBlock from './UsingCount.jsx';
const RootEl = styled.div`
	padding:0;
    width:80vw;
    max-width:80vw;
    border-bottom:0.2em solid black;
    min-height:20vmax;
    box-sizing:border-box;
    display: flex;
    @media screen and (max-width: 600px){
    	display: block;
    }
`;
const Card = styled.div`
	box-sizing:border-box;
	background:${props => props.isError ? '#dc1b86' : '#a3dcbe'};
	border:1px;
	min-width:40%;
	&:first-of-type{
		border-right:0.16em solid black;
		
	}
	&:last-of-type{
		min-width:20%;
		border-left:0.16em solid black;
		@media screen and (max-width: 600px){
			border-left:0 ;
	    	border-top:0.16em solid black;
    	}
	}
	
	color:${props => props.isError ? '#faf3e3' : '#247066'};
	overflow:hidden;
	padding:1vw;
	display:block;
`;
const HostItem = ({statusEmoji, filesOnDisk, now, host}) => {
	const ExtraData = styled.span`display:block;
		margin-top: 0;
		margin-bottom: 5px;
		text-align: center;
		min-width: 100%;
		font-size: .8vmax;
		@media screen and (max-width: 600px){
	    		font-size: 1.7vmax;
    	}
	`;
	const TopText = styled.p`
		margin-bottom: 0;
		text-align: center;
		font-size: .8vmax;
		@media screen and (max-width: 600px){
	    		font-size: 2vmax;
    	}
	`;
	return (
		<div>
			<TopText>{statusEmoji} - <a href={host}>{host}</a></TopText>
			<ExtraData>
				on disk: {filesOnDisk} -- now: {now}
			</ExtraData>
		</div>
	);
}
const Title = styled.h4`
		font-size: 2vw;
		font-weight: 600;
		letter-spacing: .01vw;
		margin-bottom: 0;
		margin-top: 0;
	`;

class Servers extends Component {
	
	state = {
		data: "",
		hasError: false
	};
	
	componentDidMount() {
		fetch('https://friendly-central-bot.glitch.me/status').then(response => response.json())
			.then(data => {
				this.setState({data: data});
			})
			.catch(e => {
				this.setState({hasError: true})
			})
	}
	
	render() {
		const {data} = this.state;
		return (
			<React.Fragment>
				{!this.state.hasError && (
					<Card>
						{(typeof data == typeof []) && data.map((item,idx) => <HostItem key={idx} {...item} />)}
					</Card>
				)}
				{this.state.hasError && (<Card isError>
					<Title>Error</Title>
				</Card>)}
			</React.Fragment>
		)
	}
}

class BottomRow extends Component {
	state = {
		data: [],
		hasError: false
	}
	
	componentDidMount() {
		fetch("https://statbotservis.glitch.me/last").then(r => r.json())
			
			.then((data) => {
				this.setState({data: data})
			})
			.catch(e => {
				this.setState({hasError: true})
			})
	}
	
	render() {
		return (
			<RootEl>
				<NewUsersChartBlock data={this.state.data} hasError={this.state.hasError}/>
				<UsingChartBlock data={this.state.data} hasError={this.state.hasError}/>
				<Servers/>
			</RootEl>
		);
	}
}

export default BottomRow;
