import styled from "styled-components";
import Card from "../cardHOC/card.component";
import React, {Component} from "react";
import MiniChart from "../miniChartComponent/miniChartComponent";


const Box = styled.div`
		display:flex;
		flex-direction:column;
		&:first-child{
			margin:auto;
		}
	`;
const Title = styled.h4`
		font-size: 2vw;
		font-weight: 600;
		letter-spacing: .01vw;
		margin-bottom: 0;
		margin-top: 0;
	`;


class UsingChartBlock extends Component {
	constructor(props){
		super(props);
		this.state.hasError = this.props.hasError
	}
	state = {
		data: [],
		hasError: true,
	};
	
	componentDidCatch(error, errorInfo) {
		this.setState({hasError:true})
	}
	
	static getDerivedStateFromError(error) {
		return {hasError: true};
	}
	getSize = ()=>{
		if(window.innerWidth > 600) return {width: 0.6 * window.innerWidth, height: 0.14 * window.innerWidth};
		else return {width: 0.75 * window.innerWidth, height: 0.4 * window.innerWidth};
	}
	render() {
		return (
			<React.Fragment>
				{!this.state.hasError && (<Card>
					<Box>
						<Title>Using</Title>
						<MiniChart size={this.getSize()} lineKey={"count"} data={this.props.data}/>
					</Box>
				</Card>)}
				{this.state.hasError && (<Card isError>
					<Box>
						<Title>Error</Title>
					
					</Box>
				</Card>)}
			</React.Fragment>
		)
	}
}

export default UsingChartBlock;